*{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.container{
    position: relative;
    width: 1440px;
    height: 1024px;
    background: #F7F7F7;
}

.topBar{
    position: absolute;
    width: 1440px;
    height: 91px;
    left: 0px;
    top: 0px;
    background: #CEC6CE;
}

.title{
    position: absolute;
    width: 161px;
    height: 34px;
    left: 28px;
    top: 124px;

    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 34px;

    color: #5D405C;
    -webkit-animation: flipTitle 1s;
            animation: flipTitle 1s; 
    
}


@-webkit-keyframes flipTitle {
    from{
        -webkit-transform: rotateX(90deg);
                transform: rotateX(90deg);
    }
    to{
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
}


@keyframes flipTitle {
    from{
        -webkit-transform: rotateX(90deg);
                transform: rotateX(90deg);
    }
    to{
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
}

.subTitle{
    position: absolute;
    width: 183px;
    height: 17px;
    left: 28px;
    top: 166px;

    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;

    color: #828282;
}


.buttonSearch{
    position: absolute;
    width: 100px;
    height: 40px;
    left: 1316px;
    top: 135px;
    background: #5D405C;
    border-radius: 10px;
    border: 0;
    
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #F7F7F7;
    
    cursor: pointer;
    transition: -webkit-transform 0.5;
    transition: transform 0.5;
    transition: transform 0.5, -webkit-transform 0.5;
}
.buttonSearch:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.main{
    position: relative;
}



/*Estilo dos gráficos*/

.boxBar{
    position: absolute;
    width: 680px;
    height: 359px;
    left: 28px;
    top: 228px;
    border-radius: 10px;
    background-color: white;
    max-width: 680px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
    
}
.boxScatter{
    position: absolute;
    width: 680px;
    height: 359px;
    left: 736px;
    top: 228px;
    border-radius: 10px;
    background-color: white;
    max-width: 680px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
    
}

.titleGraph{
    position: absolute;
    width: 55px;
    height: 20px;
    left: 23px;
    top: 21px;
    /* background-color: #6bccbc; */
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #5D405C;
}

.graph{
    width: 600px;
    height: 320px;
    margin: 35px 35px;
}

.graphScatter{
    width: 600px;
    height: 297px;
    margin: 35px 35px;
}

.info{
    position: absolute;
    left: 70px;
    right: 10.94%;
    top: 0;
    bottom: 10.94%;
    background-color: rgba(255, 255, 255, 0);
    border: 0;
    border-radius: 10px;

    width: 25px;
    height: 25px;

    
    cursor: pointer;
    transition: -webkit-transform 0.5;
    transition: transform 0.5;
    transition: transform 0.5, -webkit-transform 0.5;
}
.info:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}



